#image{
    width: 400px;
    height: 480px;
    /* border:5px solid black; */
    background-image: url('');
    background-repeat: no-repeat;
    background-size: 100%;
    margin-top: 25px;
    margin-bottom:25px;
    display: inline-block;
    margin-left: 40px;
    /* margin-left: auto;
    margin-right: auto; */
  }
  .input{
    display: none;
  }
  .amr{
    background: #d65858;
    color: #fff;
    font-size: 18px;
    width: 150px;
    text-align: center;
    border: 0;
    padding: 15px;
    margin-top: 20px;
    display: block;
    margin-left: 155px;
    /* margin-left: auto;
    margin-right: auto; */
    cursor: pointer;
    font-weight: bold;
  }
  .amr:hover {
        background: #000;
    color: #eb5424;
  }


